<template>
    <div>

        <v-app-bar class="my-header" color="#0B2D46" height="60" dark flat app>

            <div class="my-header-logo d-flex align-center" @click="openMenu">
                <!-- <v-img alt="Vuetify Logo" class="shrink mr-1" contain src="@/assets/logo.png" transition="scale-transition"
                    width="36" /> -->
                <svg-icon class="logo" icon-class="logo"></svg-icon>

                <span class="logo-text">{{title}}</span>
            </div>

            <v-spacer></v-spacer>

            <div class="my-header-menu">
                <div class="my-header-shadow-box">
                    <div ref="shadow" class="my-header-shadow"></div>
                </div>
                <ul>
                    <li v-for="menu in menuGroup" :key="menu.path" :class="{active:curIndex===menu.index}" @click="onSelectMenu(menu)">
                        <div class="menu-ele">
                            <span class="menu-title">{{menu.title}}</span>
                        </div>
                    </li>
                </ul>
            </div>

            <v-spacer></v-spacer>
        </v-app-bar>


        <v-navigation-drawer v-model="drawer" dark app>
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-title class="title">
                        {{title}}
                    </v-list-item-title>
                </v-list-item-content>
            </v-list-item>

            <v-divider></v-divider>

            <v-list dense nav>
                <v-list-item v-for="item in menuGroup" :key="item.title" @click="onSelectMenu(item)" link>
                    <v-list-item-icon>
                        <svg-icon :icon-class="item.icon"></svg-icon>
                    </v-list-item-icon>

                    <v-list-item-content>
                        <v-list-item-title>{{ item.title }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
    </div>
</template>

<script>
    import { asyncRoutes } from '@/router'
    import { title } from '@/settings'

    const WIDTH = 992

    const getCurIndex = function() {
        return Number(localStorage.getItem('curIndex'))
    }

    const setCurIndex = function(val) {
        return localStorage.setItem('curIndex', val)
    }

    export default {
        name: 'my-header',
        data() {
            return {
                menuGroup: asyncRoutes.map(v => {
                    return {
                        path: v.path,
                        index: v.meta.index,
                        title: v.meta.title,
                        icon: v.meta.icon
                    }
                }),

                firstEntry: true,
                curIndex: getCurIndex(),
                lastIndex: 0,

                drawer: false
            }
        },
        computed: {
            curPath() {
                return this.$route.path
            },
            title() {
                return title
            }
        },
        watch: {
            curPath(val) {
                const index = this.menuGroup.findIndex(v => v.path === val)

                if (index < 0) return

                this.lastIndex = this.curIndex
                this.curIndex = index
                setCurIndex(this.curIndex)

                this.moveToTarget(this.lastIndex, this.curIndex, this.firstEntry ? 0 : 100)
                this.firstEntry = false
            }
        },
        beforeMount() {
            window.addEventListener('resize', this.$_resizeHandler)
        },
        beforeDestroy() {
            window.removeEventListener('resize', this.$_resizeHandler)
        },
        mounted() {
            const isMobile = this.$_isMobile()
            if (isMobile) {
                this.$store.dispatch('app/toggleDevice', 'mobile')
            }

            this.moveToTarget(this.lastIndex, this.curIndex, this.firstEntry ? 0 : 100)
            this.firstEntry = false
        },
        methods: {
            openMenu() {
                const rect = document.body.getBoundingClientRect()
                const isMobile = rect.width + 17 < 767

                this.drawer = (isMobile && !this.drawer)
            },
            onSelectMenu(menu) {
                if (this.curPath === menu.path) {
                    // this.$router.replace({
                    //     path: '/redirect' + menu.path
                    // })
                } else {
                    this.$router.push(menu.path)
                }
            },
            moveToTarget(prev, next, timeout = 100) {
                if (timeout === 0) {
                    this.$refs.shadow.style.marginLeft = next * 127 + 'px'
                    return
                }

                let prevPos = prev * 127
                let progress = 0
                let start = null

                const targetPos = next * 127 - prevPos

                const step = (timestamp) => {
                    if (!start) start = timestamp;
                    progress = (timestamp - start) / timeout;
                    this.$refs.shadow.style.marginLeft = prevPos + progress * targetPos +
                        'px';
                    if (progress < 1) {
                        window.requestAnimationFrame(step);
                    } else {
                        this.$refs.shadow.style.marginLeft = prevPos + targetPos + 'px';
                    }
                }

                // if (this.$refs.shadow.style.marginLeft) {
                //     prevPos = this.$refs.shadow.style.marginLeft.split('px')
                // }

                window.requestAnimationFrame(step)
            },
            $_isMobile() {
                const rect = document.body.getBoundingClientRect()
                return rect.width - 1 < WIDTH
            },
            $_resizeHandler() {
                if (!document.hidden) {
                    const isMobile = this.$_isMobile()
                    this.$store.dispatch('app/toggleDevice', isMobile ? 'mobile' : 'desktop')
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @media screen and (max-width: 767px) {
        .my-header-logo {
            margin: 0 !important;
            width: 100% !important;
            
            .logo {
                position: absolute;
            }

            .logo-text {
                text-align: center !important;
                width: 100% !important;
            }
        }

        .my-header-menu {
            display: none;
        }
    }

    .my-header {
        .my-header-logo {
            position: relative;
            margin: 0 24px;
            transition: margin-left 1s;

            user-select: none;

            .logo {
                width: 36px;
                height: 36px;
                margin-right: 10px;
            }

            .logo-text {
                height: 36px;
                color: rgba(11, 167, 212, 1);
                font-size: 26px;
                font-style: italic;
                font-weight: bold;
                font-family: SourceHanSansSC-bold;
                text-align: left;
            }
        }

        .my-header-login {
            position: absolute;
            right: 0;
            height: 34px;

            .login-ele {
                margin: 0 0 0 20px;
                display: inline-flex;
                width: 150px;

                .login-input {
                    color: #F0F0F0;
                }
            }

            .login-btn {
                margin-left: 20px;
                margin-right: 80px;
                display: inline-flex;

                .v-btn {
                    border-radius: 10px;
                }
            }

            .userbox {
                line-height: 34px;
            }

            .userinfo {
                color: rgba(255, 255, 255, 1);
                font-size: 14px;
                text-align: left;
                font-family: SourceHanSansSC-regular;
            }

            .logout-btn {
                transition: margin-right 1s;

                margin: 0 20px 0 20px;
                cursor: pointer;
                color: rgba(11, 167, 212, 1);
                font-size: 14px;
                font-weight: bold;
                text-align: left;
                font-family: SourceHanSansSC-bold;
            }
        }

        .my-header-menu {
            .my-header-shadow-box {
                position: absolute;
                top: 0;
            }

            .my-header-shadow {
                background-color: #0BA7D4;
                font-family: SourceHanSansSC-bold;
                width: 80px;
                height: 60px;
                position: absolute;
                z-index: -1;
                animation: show .5s ease-in;
                border-left: 1px solid #0BA7D4;
                border-right: 1px solid #0BA7D4;

                &::before {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 0;
                    margin-left: -47px;
                    border-bottom: 60px solid #0BA7D4;
                    border-left: 47px solid transparent;
                }

                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 0;
                    margin-left: 78px;
                    border-top: 60px solid #0BA7D4;
                    border-right: 47px solid transparent;
                }
            }

            ul {
                display: inline-flex;
                list-style-type: none;
                padding: 0;

                li {
                    cursor: pointer;
                    user-select: none;

                    &:not(:last-child) {
                        margin: 0 47px 0 0;
                    }

                    &.active {
                        .menu-title {
                            font-weight: bold;
                            opacity: 1;
                        }
                    }
                }

                .menu-ele {
                    width: 80px;
                    height: 50px;

                    .menu-title {
                        height: 50px;
                        line-height: 50px;
                        opacity: 0.8;
                        color: rgba(255, 255, 255, 1);
                        font-size: 18px;
                        font-style: italic;
                        text-align: center;
                        font-family: SourceHanSansSC-regular;
                    }

                    span {
                        display: block;
                    }


                }
            }
        }
    }

    @keyframes show {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }
</style>
